'use client';

import React from 'react';
import { motion } from 'framer-motion';
import Link from 'next/link';
import Image from 'next/image';
import { ArrowRight, Mountain, Palmtree, Building2, MapPin } from 'lucide-react';
import { siteData, siteDataFr } from '@/data/siteData';

export const TourCategories = ({ locale, dict }: { locale: string, dict: any }) => {
    const data = locale === 'fr' ? siteDataFr : siteData;

    const categories = [
        {
            id: 'toubkal-treks',
            name: 'Toubkal Treks',
            description: 'Conquer North Africa\'s highest peak and explore Berber villages',
            icon: Mountain,
            tours: data.toubkalTreks,
            image: data.toubkalTreks[0]?.image || '/images/toubkal_treks/placeholder.png',
            color: 'from-secondary to-secondary-dark',
            count: data.toubkalTreks.length
        },
        {
            id: 'desert-tours',
            name: 'Desert Tours',
            description: 'Experience the magic of the Sahara and southern Morocco',
            icon: Palmtree,
            tours: data.desertTours,
            image: data.desertTours[0]?.image || '/images/desert_tours/placeholder.png',
            color: 'from-primary to-primary-dark',
            count: data.desertTours.length
        },
        {
            id: 'imperial-cities',
            name: 'Imperial Cities',
            description: 'Discover Morocco\'s rich history and cultural heritage',
            icon: Building2,
            tours: data.imperialCities,
            image: data.imperialCities[0]?.image || '/images/imperial_cities/placeholder.png',
            color: 'from-accent to-accent-dark',
            count: data.imperialCities.length
        },
        {
            id: 'excursions',
            name: 'Day Excursions',
            description: 'Perfect day trips from Marrakech to nearby attractions',
            icon: MapPin,
            tours: data.excursions,
            image: data.excursions[0]?.image || '/images/excursions/placeholder.png',
            color: 'from-berber-terracotta to-primary-dark',
            count: data.excursions.length
        }
    ];

    return (
        <section className="py-20 bg-surface-off">
            <div className="container mx-auto px-6 md:px-4">

                {/* Header - Centered */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="text-center mb-16"
                >
                    <span className="text-accent text-sm font-semibold mb-3 block uppercase tracking-wider">
                        {dict?.subtitle || "Explore Morocco"}
                    </span>
                    <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-neutral-dark mb-6">
                        Our <span className="text-primary">Adventures</span>
                    </h2>
                    <p className="text-neutral-medium text-lg max-w-2xl mx-auto">
                        From mountain peaks to desert dunes, discover authentic Morocco with expert local guides
                    </p>
                </motion.div>

                {/* Category Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6 lg:gap-8">
                    {categories.map((category, index) => {
                        const Icon = category.icon;
                        return (
                            <motion.div
                                key={category.id}
                                initial={{ opacity: 0, y: 30 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                viewport={{ once: true }}
                                transition={{ delay: index * 0.1 }}
                                className="group"
                            >
                                <Link href={`/${locale}/tours?category=${category.id}`}>
                                    <div className="relative h-[400px] md:h-[450px] rounded-2xl overflow-hidden shadow-xl hover:shadow-2xl transition-all duration-500">
                                        {/* Background Image */}
                                        <Image
                                            src={category.image}
                                            alt={category.name}
                                            fill
                                            className="object-cover group-hover:scale-110 transition-transform duration-700"
                                        />

                                        {/* Gradient Overlay */}
                                        <div className={`absolute inset-0 bg-gradient-to-br ${category.color} opacity-60 group-hover:opacity-70 transition-opacity duration-500`} />

                                        {/* Content */}
                                        <div className="absolute inset-0 p-8 flex flex-col justify-between">
                                            {/* Top - Icon & Count */}
                                            <div className="flex items-start justify-between">
                                                <div className="w-14 h-14 rounded-2xl bg-white/20 backdrop-blur-md flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                                                    <Icon className="w-7 h-7 text-white" strokeWidth={2.5} />
                                                </div>
                                                <span className="bg-white/20 backdrop-blur-md text-white px-4 py-2 rounded-full text-sm font-semibold">
                                                    {category.count} Tours
                                                </span>
                                            </div>

                                            {/* Bottom - Title & Description */}
                                            <div>
                                                <h3 className="text-3xl md:text-4xl font-bold text-white mb-3 group-hover:translate-x-2 transition-transform duration-300">
                                                    {category.name}
                                                </h3>
                                                <p className="text-white/90 text-base mb-6 leading-relaxed">
                                                    {category.description}
                                                </p>

                                                {/* CTA Button */}
                                                <div className="inline-flex items-center gap-2 bg-white text-neutral-dark px-6 py-3 rounded-full font-semibold group-hover:bg-primary group-hover:text-white transition-all duration-300">
                                                    <span>Explore {category.name}</span>
                                                    <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </motion.div>
                        );
                    })}
                </div>

                {/* Featured Tours Preview - Horizontal Scroll */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="mt-16"
                >
                    <h3 className="text-2xl md:text-3xl font-bold text-neutral-dark mb-8 text-center">
                        Featured <span className="text-primary">Experiences</span>
                    </h3>

                    <div className="relative -mx-6 md:mx-0">
                        <div className="flex gap-5 overflow-x-auto pb-6 px-6 md:px-0 snap-x snap-mandatory scrollbar-hide">
                            {/* Show 2 tours from each category */}
                            {[
                                ...data.toubkalTreks.slice(0, 2),
                                ...data.desertTours.slice(0, 2),
                                ...data.imperialCities.slice(0, 1),
                                ...data.excursions.slice(0, 1)
                            ].map((tour: any, i: number) => (
                                <motion.div
                                    key={tour.id}
                                    initial={{ opacity: 0, x: 50 }}
                                    whileInView={{ opacity: 1, x: 0 }}
                                    viewport={{ once: true }}
                                    transition={{ delay: i * 0.05 }}
                                    className="flex-shrink-0 w-[280px] md:w-[320px] snap-start"
                                >
                                    <Link href={`/${locale}/tours/${tour.id}`} className="block group">
                                        <div className="relative h-[380px] rounded-2xl overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300">
                                            {/* Background Image */}
                                            <Image
                                                src={tour.image}
                                                alt={tour.name}
                                                fill
                                                className="object-cover group-hover:scale-105 transition-transform duration-500"
                                            />

                                            {/* Gradient Overlay */}
                                            <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent" />

                                            {/* Duration Badge */}
                                            <div className="absolute top-4 right-4">
                                                <span className="bg-white/90 backdrop-blur-sm text-neutral-dark px-3 py-1.5 rounded-full text-xs font-semibold">
                                                    {tour.duration.replace('_', ' ')}
                                                </span>
                                            </div>

                                            {/* Bottom Content */}
                                            <div className="absolute bottom-0 left-0 right-0 p-5">
                                                <h4 className="text-lg font-bold text-white mb-2 line-clamp-2 leading-tight">
                                                    {tour.name}
                                                </h4>

                                                {tour.pricing && tour.pricing.length > 0 && tour.pricing[0].totalPrice && (
                                                    <div className="flex items-baseline gap-1">
                                                        <span className="text-white/70 text-xs">From</span>
                                                        <span className="text-white text-xl font-bold">€{tour.pricing[0].totalPrice}</span>
                                                        <span className="text-white/70 text-xs">/person</span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </Link>
                                </motion.div>
                            ))}
                        </div>

                        {/* Gradient Fade - Right */}
                        <div className="absolute right-0 top-0 bottom-6 w-20 bg-gradient-to-l from-surface-off to-transparent pointer-events-none hidden md:block" />
                    </div>
                </motion.div>
            </div>
        </section>
    );
};
